<template>
  <div class="index">
    <div class="content">
      <div class="top-box">
        <div class="title">盛发康</div>
        <div class="desc">
          <span>川西绵竹中医世家</span>
          <br />
          <span>生发、黑发、养发</span>
        </div>
      </div>
      <el-tabs v-model="activeName" @tab-click="changeTab">
        <el-tab-pane
          v-for="(item, index) in tabList"
          :key="index"
          :label="item.label"
          :name="item.name">
        </el-tab-pane>
      </el-tabs>
      <router-view/>
