    <div class="footer">© Copyright 2020. 盛发康</div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      activeName: 'about',
      tabList: [
        { label: '关于我们', name: 'about' },
        { label: '洗护系列', name: 'product' },
        { label: '联系我们', name: 'contact' },
        { label: '关于公司', name: 'company' }
      ]
    }
  },
  created () {
    this.activeName = this.$route.name
  },
  methods: {
    // 改变顶部tab
    changeTab (tab) {
      this.$router.push({
        name: tab.name
      })
    }
  }
}
</script>

