$topBoxHeight: 80px;
$descMargin: 40px;
$footerHeight: 40px;
.index {
  width: 100%;
  height: 100%;
  &>.content {
    width: 100%;
    height: auto;
    min-height: calc(100% - 60px);
    padding: 0 20px;
    box-sizing: border-box;
    overflow: hidden;
    &>.top-box {
      width: 100%;
      height: $topBoxHeight;
      padding: 0 70px;
      box-sizing: border-box;
      display: flex;
      align-items: center;
      justify-content: space-between;
      &>div {
        &.title {
          font-size: 34px;
          font-weight: bold;
        }
        &.desc {
          font-size: 20px;
          overflow: hidden;
          &>span {
            &:nth-of-type(1) {
              margin-right: $descMargin;
            }
            &:nth-of-type(2) {
              margin-left: $descMargin;
            }
          }
        }
